import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ReferenceLine,
  Label,
  Brush,
} from "recharts"

import { useState, useRef, createRef } from "react"

import { geoPath, geoMercator, scaleSymlog } from "d3"

import { geoVoronoi } from "d3-geo-voronoi"

import moment from "moment"

import chroma from "chroma-js"

import "moment/locale/fr"
import "moment/locale/en-gb"

import * as shapeFrance from "./france-shape.json"

export default function Chart({ water, cities }) {
  const [showCity, setShowCity] = useState([])
  const [tempHover, setTempHover] = useState([])
  const [scaleChoice, setScaleChoice] = useState("linear")

  /**
   *
   * @param {*} city
   * @returns
   */
  const weekCity = (city, week = waterValues.length - 1) =>
    waterValues[week][city]

  /**
   *
   * @param {*} param0
   * @returns
   */
  const getUniqueName = ({ commune, nom }) =>
    multipleStation.includes(commune) ? nom : commune

  /**
   *
   * @param {*} weeks
   * @param {*} filters
   * @returns
   */
  const filterWaterValue = (weeks = [], filters = []) =>
    weeks.map((week, index) =>
      Object.entries(week)

        .map(([city, value], index, all) => {
          if (filters.includes(city) || city === xAxis) {
            return [city, value]
          } else {
            return [city, 0]
          }
        })
        .reduce(
          (accumulator, [name, value]) => ({ ...accumulator, [name]: value }),
          {}
        )
    )

  /*

  Listener used both with d3 and react component

  */
  /**
   *
   * @param {*} event
   */
  const handleMouseOut = (event) => {
    event?.preventDefault?.()
    const city =
      event?.currentTarget?.getAttribute("data-unique") ||
      event.value ||
      event.name
    setTempHover((old) => {
      return old.filter((name) => name !== city)
    })
  }

  /**
   *
   * @param {*} event
   */
  const handleMouseOver = (event) => {
    event?.preventDefault?.()
    const city =
      event?.currentTarget?.getAttribute("data-unique") ||
      event.value ||
      event.name
    setTempHover((old) => {
      return [...old, city]
    })
  }

  /**
   *
   * @param {*} event
   */
  const handleclick = (event) => {
    const city =
      event?.currentTarget?.getAttribute("data-unique") ||
      event.value ||
      event.name
    if (filter.includes(city)) {
      setShowCity(filter.filter((name) => name !== city))
    } else {
      setShowCity([...filter, city])
      setTempHover((old) => {
        return [city]
      })
    }
  }

  /**
   *
   * @param {*} generatedVoronoi
   * @param {*} projection
   * @param {*} mask
   * @returns
   */
  const generatePaths = (
    generatedVoronoi,
    projection,
    mask = false,
    neon = false
  ) =>
    generatedVoronoi.features.map((cellPolygon, index) => (
      <path
        key={index}
        fill={
          neon
            ? "none"
            : levelToColor(weekCity(cellPolygon.properties.site.info.unique))
        }
        data-name={cellPolygon.properties.site.info.nom}
        data-city={cellPolygon.properties.site.info.commune}
        data-unique={cellPolygon.properties.site.info.unique}
        d={geoPath().projection(projection)(cellPolygon)}
        className={(() => {
          const city = cellPolygon.properties.site.info.unique
          return (
            ((filter && filter?.includes(city) && " selected ") || "") +
            ((tempHover && tempHover?.includes(city) && " activated ") || "") +
            (neon ? " station-neon" : " station")
          )
        })()}
        onMouseLeave={handleMouseOut}
        onMouseEnter={handleMouseOver}
        onClick={handleclick}
        mask={
          mask
            ? `url(#mask-${cellPolygon.properties.site.info.unique.replaceAll(
                " ",
                "-"
              )})`
            : ""
        }
      ></path>
    ))

  /**
   *
   * @param {*} generatedVoronoi
   * @param {*} projection
   * @returns
   */
  const generateMasks = (generatedVoronoi, projection) =>
    generatedVoronoi.features.map((cellPolygon, index) => (
      <mask
        key={index}
        id={`mask-${cellPolygon.properties.site.info.unique.replaceAll(
          " ",
          "-"
        )}`}
      >
        <path
          data-name={cellPolygon.properties.site.info.nom}
          fill="white"
          d={geoPath().projection(projection)(cellPolygon)}
        />
      </mask>
    ))

  /**
   *
   * @param {*} generatedVoronoi
   * @param {*} projection
   * @param {*} hide
   * @returns
   */
  const generateTexts = (generatedVoronoi, projection, hide) =>
    generatedVoronoi.features
      .filter(
        (cellPolygon) =>
          !hide || !idfCities.includes(cellPolygon.properties.site.info.commune)
      )
      // since there isnt zindex in svg need to reorder the dom to manage who is on top of the pile
      // here the onhover are automatically on top of the pile and wont overlap with siblings position
      .sort((cellPolygon) =>
        tempHover.includes(cellPolygon.properties.site.info.unique) ? 1 : -1
      )
      .map((cellPolygon, index) => (
        <text
          key={index}
          className={
            "france-svg-label " +
            (tempHover.includes(cellPolygon.properties.site.info.unique)
              ? "activated"
              : "")
          }
          data-name={cellPolygon.properties.site.info.nom}
          data-city={cellPolygon.properties.site.info.commune}
          data-unique={cellPolygon.properties.site.info.unique}
          onMouseLeave={handleMouseOut}
          onMouseEnter={handleMouseOver}
          onClick={handleclick}
          x={projection(cellPolygon.properties.sitecoordinates)[0]}
          y={projection(cellPolygon.properties.sitecoordinates)[1]}
        >
          <tspan>
            {(() => {
              const name = getUniqueName(cellPolygon.properties.site.info)
              return weekCity(name) > weekCity(name, waterValues.length - 2)
                ? "🠉"
                : ""
            })()}
          </tspan>
          {/* <tspan className="label-city">
            {(tempHover.includes(cellPolygon.properties.site.info.unique) &&
              cellPolygon.properties.site.info.unique) ||
              ""}
          </tspan> */}
        </text>
      ))

  /*

      dual language mgt both french and english based on users conf

  */
  const french = navigator.language === "fr"
  if (french) {
    moment.locale("fr")
  }

  const displayEaster = document?.location?.hash === "#graph"
  const xAxis = "semaine"

  // todo autodetect multiple station
  const multipleStation = ["paris"]

  // used for zoom on paris suburban
  const idfCities = [
    "paris",
    "versailles",
    "sarcelles",
    "lagny-sur-marne",
    "evry-courcouronnes",
  ]

  /*

  Station management

  */

  const initCities = cities.map(({ commune, nom, ...rest }) => ({
    ...rest,
    commune: commune.toLowerCase(),
    nom: nom.toLowerCase(),
  }))

  let citiesName = initCities
    .map(({ commune }) => commune)
    .filter((name) => !multipleStation.includes(name))

  // waterValues alterate water data by switching station name to city name
  const waterValues = water?.map((week) =>
    Object.entries(week)
      .map(([station, value]) => [station.toLowerCase(), value])
      // remove undefined values for a city this week
      .filter(([, value]) => value !== "NA")
      // format the city name according to cities dataset
      .map(([station, value]) => {
        const city = initCities?.find(({ nom }) => station === nom)?.commune
        // graphique name is the city name excepted if multiples stations per cities then we will
        // use the station name directly since he is allready prefixed by city name
        const name =
          !station.includes("national") &&
          station !== xAxis &&
          !multipleStation.includes(city)
            ? city
            : station
        if (multipleStation.includes(city)) {
          citiesName.push(station)
        }
        return [name, value]
      })
      .reduce((accumulator, [name, value]) => {
        // there is both National_54 and National_12 but sequential dealing before and after december 2023
        // when new 54 stations added
        name = name.includes("national") ? "national" : name
        return {
          ...accumulator,
          [name]:
            name === xAxis
              ? // date or water value by default
                moment(value, "YYYY-W")
                  .add(7, "d")
                  .format(french ? "YYYY-DD-MM" : "YYYY-MM-DD")
              : Number(value.split(",")[0]),
        }
      }, {})
  )
  // remove twins done to autodetect of multiple station per city then order alphabeticaly
  citiesName = [...new Set(citiesName)].sort()
  // set national in top position of the object
  if (citiesName.length > 0) citiesName.unshift("national")

  const refs = useRef(citiesName.map(() => createRef()))
  const [brushChoice, setBrushChoice] = useState({
    startIndex: 0,
    endIndex: waterValues.length - 1,
  })

  /*

  Color Management

  */

  // generate palet of colors
  const cityColors = chroma
    .cubehelix()
    .start(0)
    .rotations(0.8)
    .lightness(0.5)
    .gamma(0.8)
    .hue(4)
    .scale()
    .colors(citiesName.length)

  // scale for map visualisation of covid level. its based on Atom One Dark theme colors
  const scaleColors = [
    "#98c379", // green 0-1000
    "#98c379", // green 0-1000
    "#e5c07b", // yellow
    "#e5c07b", // yellow
    "#db8b6b", // orange
    "#ce4646", // red
  ]
  const scale = chroma.scale(scaleColors).colors(16)
  // TODO try "scale sequential" from d3 because should use my own
  const max = waterValues
    .map(
      (week) =>
        Object.entries(week)
          .filter(([city, value]) => city !== xAxis)
          .sort(([city, value], [anotherCity, anotherValue]) => {
            return value > anotherValue ? -1 : 1
          })[0][1]
    )
    .sort((week, nextWeek) => {
      return week > nextWeek ? -1 : 1
    })[0]

  const log = scaleSymlog()
    .domain([0, Math.floor(max)])
    .range([0, 16])
    .constant(200)

  const levelToColor = function (level) {
    log.range([0, 16]) // dont know why logOp.range is changed from 0-16 to 0-*** i need to reset it
    return scale[Math.floor(log(Math.floor(level)))] || scale[scale.length - 1]
  }

  /*

  filtering

  */

  // extract max of latestweek to display in the top of graph, more the biggest known cities

  const lastWorst = Object.entries(waterValues.slice(-1)?.[0]).sort(
    ([nameA, valueA], [nameB, valueB]) => valueB - valueA
  )?.[1]?.[0]

  // initial filter is National, 3 biggest cities and the zone with the max value
  const initialFilter = [
    ...new Set([
      "national",
      "paris seine-centre",
      "marseille",
      "lyon",
      "nice",
      "toulouse",
      "nantes",
      "montpellier",
      "strasbourg",
      "bordeaux",
      "lille",
      "rennes",
      lastWorst,
    ]),
  ]

  // build filter:  user choice or fallback to initial filter
  const filter = showCity.length === 0 ? initialFilter : showCity

  // usable filter is both setted filter and interaction filter as mouseover
  const tempFilter = tempHover ? [...filter, ...tempHover] : filter

  const filtered = filterWaterValue(waterValues, tempFilter)

  /*

  Map visualisation
  so this list is only build for existing cities in the current LAST WEEK, need to do a proper looping for previous ones

  */

  const citiesCoordinates = initCities
    .filter((city) => weekCity(getUniqueName(city)))
    .map(({ latitude, longitude, ...rest }) => ({
      type: "Feature",
      properties: {},
      info: {
        ...rest,
        unique: getUniqueName(rest),
      },
      geometry: {
        type: "Point",
        coordinates: [
          parseFloat(longitude.replace(",", ".")),
          parseFloat(latitude.replace(",", ".")),
        ],
      },
    }))

  const styles = {
    width: 300,
    height: 350,
  }

  const mercator = geoMercator()
    .scale(1100) // zoom
    .center([2, 46.5]) // Centers the map on the center of France
    .translate([styles.width / 2, styles.height / 2])
    // pretty cool, auto fix the zoom and fit
    .fitExtent(
      [
        [0, 0],
        [styles.width - 0, styles.height - 0],
      ],
      shapeFrance
    )

  const mercatorZoom = geoMercator()
    .scale(5000) // zoom
    .center([2.1, 48.5]) // Centers the map on the center of France
    .translate([styles.width / 2, styles.height / 2])

  const generatedVoronoi = geoVoronoi().polygons(citiesCoordinates)

  const idfVoronoi = {
    ...generatedVoronoi,
    features: generatedVoronoi.features.filter((cellPolygon) =>
      idfCities.includes(cellPolygon.properties.site.info.commune)
    ),
  }
  /*

  easters egg statics

  */

  const easters = [
    {
      x: "2022-09-05",
      fr: "❝C'est un rhume❞",
      en: "❝It's mild❞",
      position: "insideBottom",
    },
    {
      x: "2022-10-17",
      fr: "❝un soubressaut qui sait❞",
      en: "❝just a bump who knows❞",
      position: "center",
    },
    {
      x: "2022-12-12",
      fr: "❝Personne n'aurait pu prévoir ça❞",
      en: "❝No one could foreseen this❞",
      position: "insideTop",
    },
    {
      x: "2023-02-13",
      fr: "❝C'est saisonnier❞",
      en: "❝It's seasonal❞",
      position: "insideBottom",
    },
    {
      x: "2023-05-01",
      fr: "❝5 mois de mémoire immunitaire, incroyable❞",
      en: "❝5 monthes of immune memory, incredible❞",
      position: "center",
    },
    {
      x: "2023-07-10",
      fr: "❝C'est terminé❞",
      en: "❝It's over❞",
      position: "insideBottom",
    },

    {
      x: "2023-12-18",
      fr: "❝Personne n'aurait pu prédire ça❞",
      en: "❝No one could have predicted this❞",
      position: "insideTop",
    },
    {
      x: "2023-09-04",
      fr: "❝Imprévisible cocktail des variants d'été❞",
      en: "❝Summer variant soup was unpredictable❞",
      position: "center",
    },
    {
      x: "2024-04-08",
      fr: "❝Immunité de masse❞",
      en: "❝Herd immunity❞",
      position: "insideBottom",
    },
    {
      x: "2024-06-24",
      fr: "❝Un variant en été, on l'a toujours pas vu venir❞",
      en: "❝A new summer variant, we still not saw it coming❞",
      position: "insideRight",
    },
    {
      x: "2024-10-14",
      fr: "❝Nouveau combo: double vague en série❞",
      en: "❝New combo: double serial wave❞",
      position: "insideRight",
    },
    {
      x: "2024-12-24",
      fr: "❝Nouveau combo: triple vague en série❞",
      en: "❝New combo: triple serial wave❞",
      position: "insideRight",
    },
    {
      x: "2024-01-30",
      fr: "❝gneu gneu saisonier etc❞",
      en: "❝gna gna seasonal etc❞",
      position: "insideRight",
    },
    {
      x: "2025-05-01",
      fr: "❝Même mon chien apprend plus vite de ses erreurs❞",
      en: "❝Even my dog learn faster from his mistakes❞",
      position: "insideRight",
    },
  ]

  return (
    <div className="dashboard">
      <section id="cartography" className="carto">
        <header className="info">
          {french ? "Dernières données disponibles:" : "Last data available:"}
          <time
            className="date"
            dateTime={moment(
              weekCity(xAxis),
              french ? "YYYY-DD-MM" : "YYYY-MM-DD"
            ).format("YYYY-MM-DD")}
          >
            {moment(
              weekCity(xAxis),
              french ? "YYYY-DD-MM" : "YYYY-MM-DD"
            ).format("DD MMMM YYYY")}
          </time>
          <br />
          {french ? "Preset" : "Preset"}:
          <button
            className="reset"
            data={{}}
            onClick={(e) => {
              setShowCity(citiesName)
            }}
          >
            {french ? "Toutes les villes" : "All cities"}
          </button>{" "}
          <button
            className="reset"
            data={{}}
            onClick={(e) => {
              setShowCity(initialFilter)
            }}
          >
            {french ? "Grandes villes" : "Bigest cities"}
          </button>{" "}
          <button
            className="reset"
            data={{}}
            onClick={(e) => {
              setShowCity(["national"])
            }}
          >
            {french ? "Reset" : "Reset"}
          </button>{" "}
        </header>
        {/* Map Vizualisation */}
        <svg className="france-svg">
          <defs>
            {/* ok here "SourceGraphic" and "SourceAlpha" is a mantory term for "this" element. */}

            <filter
              id="neon"
              x="-100%"
              y="-100%"
              width="400%"
              height="400%"
              filterUnits="objectBoundingBox"
              primitiveUnits="userSpaceOnUse"
              colorInterpolationFilters="sRGB"
            >
              <feGaussianBlur
                stdDeviation="8 8"
                x="0%"
                y="0%"
                width="100%"
                height="100%"
                in="SourceGraphic"
                edgeMode="none"
                result="blur"
              />
              <feBlend in="SourceGraphic" in2="blur" mode="normal" />
            </filter>
          </defs>
          <path
            strokeWidth="2px"
            // filter="url(#drop-shadow)"
            d={geoPath().projection(mercator)(shapeFrance)}
          ></path>
          {/* clipPath is for france shape */}
          <clipPath id="clip">
            <path d={geoPath().projection(mercator)(shapeFrance)}></path>
          </clipPath>
          {/* mask is for city shape */}

          {generateMasks(generatedVoronoi, mercator)}

          <g clipPath="url(#clip)" fill="transparent" pointerEvents="all">
            {generatePaths(generatedVoronoi, mercator, true)}
            {generatePaths(generatedVoronoi, mercator, false, true)}
          </g>
          {/* <g clipPath="url(#clip)" fill="transparent" pointerEvents="all"> */}
          {/* </g> */}
          {generateTexts(generatedVoronoi, mercator, true)}
        </svg>
        {
          <div className="counter">
            <span className={"city "}>
              {tempHover?.[0] ||
                (french ? "Moyenne nationale" : "National Average")}
            </span>
            <span
              className={"concentration "}
              style={{
                background: levelToColor(
                  tempHover.length > 0
                    ? weekCity(tempHover?.[0])
                    : weekCity("national")
                ),
              }}
            >
              <span className="">
                {tempHover.length > 0
                  ? weekCity(tempHover?.[0])
                  : weekCity("national")}
              </span>
            </span>
          </div>
        }
        <svg className="france-svg idf-svg">
          <text x="50" y="20" fill="white">
            Ile-de-France
          </text>
          {generatePaths(idfVoronoi, mercatorZoom)}
          {generatePaths(idfVoronoi, mercatorZoom, false, true)}
          {generateTexts(idfVoronoi, mercatorZoom)}
        </svg>
        {/*

          Scale

         */}

        <div className="carto-legend">
          <div className="carto-legend-title">
            {french ? "Echelle" : "Scale "} (cg/L)
          </div>
          {log.ticks(5).map((tick, index, all) => (
            <div className="carto-legend-element" key={index}>
              <div
                className="carto-legend-scale"
                style={{ background: scale[Math.floor(log(tick))] }}
              >
                {tick > 0 ? tick / 1000 + "K" : tick}
              </div>
            </div>
          ))}
        </div>
      </section>
      {/*

      Graphic with recharts

      */}
      <section id="graph" className="graph">
        <ResponsiveContainer
          width="90%"
          height={"100%"} // will set max-height for his subwrapper
          minHeight={700}
          maxHeight={"10%"}
          style={{ resize: "both", overflow: "visible", height: "100%" }}
        >
          <LineChart
            data={filtered}
            margin={{
              right: 30,
              left: 20,
            }}
          >
            {citiesName.map((city, index, all) => (
              <Line
                type="monotone"
                connectNulls
                dot={false}
                dataKey={city}
                className={!tempFilter?.includes(city) && "desactivated"}
                key={city}
                ref={refs.current[index]}
                stroke={
                  tempFilter?.includes(city)
                    ? cityColors[index]
                    : "rgb(122, 122, 122)"
                }
                strokeWidth={tempHover && tempHover.includes(city) ? 10 : 2}
                strokeDasharray={city === "national" ? "4 4" : "0 0"}
                name={city}
                id={city}
                isAnimationActive={true}
                animationDuration={100}
                onMouseEnter={handleMouseOver}
                onMouseLeave={handleMouseOut}
              ></Line>
            ))}

            <CartesianGrid strokeDasharray="3 3" />
            <XAxis tickMargin={5} dataKey={xAxis} />
            <YAxis
              scale={scaleChoice === "log" ? log : "auto"}
              domain={[0, Math.floor(max / 1000) * 1000 + 1000]}
              tickFormatter={(doo) => (doo > 1000 ? doo / 1000 + "K" : doo)}
              type="number"
              label={{
                value: french
                  ? "Concentration virale cg/L"
                  : "Viral concentration cg/L",
                angle: -90,
                position: "left",
                textAnchor: "middle",
              }}
              tick={({ x, y, width, height, payload }) => (
                <g>
                  <rect
                    x={x - 38}
                    y={y - 10}
                    width={width / 1.4}
                    height={20}
                    fill={levelToColor(payload.value)}
                    rx={4}
                  >
                    {" "}
                  </rect>
                  <text
                    orientation="left"
                    width={width}
                    height={height}
                    stroke="none"
                    x={x}
                    y={y}
                    className="recharts-text recharts-cartesian-axis-tick-value"
                    textAnchor="end"
                    fill="white"
                  >
                    <tspan x={x} dy="0.355em">
                      {payload.value > 1000
                        ? payload.value / 1000 + "K"
                        : payload.value}
                    </tspan>
                  </text>
                </g>
              )}
            ></YAxis>

            <text
              // fill="white"
              className="scale-switch"
              transform="rotate(-90)"
              x="-50"
              y="20"
              onClick={(event) => {
                event.preventDefault()
                setScaleChoice(scaleChoice === "log" ? "linear" : "log")
              }}
            >
              {scaleChoice}
            </text>

            <Legend
              width={97}
              align="center"
              wrapperStyle={{
                width: "98%",
              }}
              onMouseEnter={handleMouseOver}
              onMouseLeave={handleMouseOut}
              onClick={handleclick}
            />
            <Tooltip
              offset={40}
              contentStyle={{
                background: "rgb(33, 37, 43)",
                textAlign: "left",
              }}
              content={({ active, payload, label }) =>
                active &&
                payload &&
                label && (
                  <div className="recharts-default-tooltip">
                    <p className="recharts-tooltip-label">
                      {moment(
                        label,
                        french ? "YYYY-DD-MM" : "YYYY-MM-DD"
                      ).format("DD MMMM YYYY")}
                    </p>
                    <ul className="recharts-tooltip-item-list">
                      {payload
                        .sort((first, second) => second.value - first.value)
                        .map(
                          (line, index) =>
                            line.value > 0 && (
                              <li key={index} className="recharts-tooltip-item">
                                <span
                                  className="recharts-tooltip-item-circle"
                                  style={{
                                    background: line.color,
                                  }}
                                ></span>{" "}
                                <span className="recharts-tooltip-item-name">
                                  {line.name}
                                </span>{" "}
                                <span
                                  className="recharts-tooltip-item-value"
                                  style={{
                                    background: levelToColor(line.value),
                                  }}
                                >
                                  {line.value}
                                </span>
                              </li>
                            )
                        )}
                    </ul>
                  </div>
                )
              }
            />
            {displayEaster &&
              scaleChoice === "linear" &&
              initialFilter.length === filter.length &&
              easters.map(({ x, position, fr, en }, index, all) => (
                <ReferenceLine
                  key={index}
                  x={moment(x, "YYYY-MM-DD").format(
                    french ? "YYYY-DD-MM" : "YYYY-MM-DD"
                  )}
                  stroke="transparent"
                  label={
                    <Label
                      width={120}
                      offset={
                        (position === "insideTop" && 15) ||
                        (position === "insideBottom" && 70) ||
                        0
                      }
                      value={french ? fr : en}
                      position={position}
                    />
                  }
                />
              ))}
          </LineChart>
        </ResponsiveContainer>
      </section>
    </div>
  )
}
